import axios from "axios";
import store from "../store";
import { check } from "./type";
import csv2json from "csvtojson";

export function getAllData(force: boolean = false) {
	const config = store.state.config;

	if (config.length <= Object.keys(store.state.data).length && !force) {
		return;
	}

	config.forEach(async (item, index) => {
		if (typeof item.data !== "string" && check.isJSON(item.data)) {
			store.state.data = {
				...store.state.data,
				[String(index)]: item.data,
			};
			return;
		}

		const res = await axios.get(item.data);
		const keys = item.config.map((e) => e.label);
		const data = await (
			await await csv2json().fromString(keys.join(",") + "\n" + res.data)
		).filter((e) => !check.isObjectFullOfNull(e));

		store.state.data = {
			...store.state.data,
			[String(index)]: data,
		};
	});
}
